.Footer {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 300px;
    grid-template-areas:
            "base";
    grid-area: F;
    background-color: palegreen;
    grid-column: 1/-1;
}

.base {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas:
     "sp7"
     "pod"
     "sp8";
    height: 200px;
    border: 2px solid black;
    grid-column: 2/12;
    align-self: center;
}

.sp7 {
    background-color: gainsboro;
    grid-column: 6/8;
    text-align: center;
    align-self: start;
    color: black;
    font-weight:bold;
    font-size:20px;

}

.foot {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas: "f1 .f2 . f3 . f1";
    grid-column: -4/-10;
    border: 2px solid black;
}

.sp8 {
    background-color: gainsboro;
    grid-column: 5/9;
    text-align: center;
    align-self: end;
    color: black;
    font-weight:bold;
    font-size:20px;
}

.f1 {
    background-color: lightgray;
    grid-column: 1/2;
}

.f2 {
    background-color: lightgray;
    grid-column: 3/4;
}

.f3 {
    background-color: lightgray;
    grid-column: 5/6;
}

.f4 {
    background-color: lightgray;
    grid-column: 7/8;
}

