.skill {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 600px;
    grid-template-areas:
            ". base base base base base base base base base base .";
    background-color: palegreen;
    grid-column: 1/-1;
}

.base {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 30% 70%;
    grid-template-areas:
            "sb1"
            "sb2";
    height: 500px;
    grid-column: -12/-2;
    align-self: center;
    border: 2px solid black;
}

.sb1 {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "sb11";
    grid-column: 1/-1;
}

.sb11 {
    grid-column: 4/6;
    align-self: start;
    font-weight:bold;
    font-size:20px;
    text-align: center;
    background-color: gainsboro;
    height: 30px;
}

.sb2 {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "sb21 . sb22 . sb23";
    grid-column: 1/-1;
    border-top: 2px solid black;
}

.sb21 {
    background-color: white;
    grid-column: 1/3;
    border-right: 2px solid red;
}

.sb22 {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 60% 40%;
    grid-template-areas:
            "sb22a"
            "sb22b";
    background-color: white;
    grid-column: 4/6;
}

.sb22a {
    border: 2px solid red;
    border-top: none;
}

.sb22b {
    background-color: gainsboro;
    border: 2px solid blue;
    border-bottom: none;
}
.sb23 {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 60% 40%;
    grid-template-areas:
            "sb23a"
            "sb23b";
    background-color: white;
    grid-column: 7/9;
}

.sb23a {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
            "sb23aa"
            "sb23ab";
}

.sb23aa {
    background-color: gainsboro;
    grid-column: 2/3;

}

.sb23ab {
    font-weight:bold;
    font-size:22px;
    text-align: center;
    background-color: gainsboro;
    grid-column: 2/3;
    align-self: center;
}

.sb23b {
    font-weight:bold;
    text-align: center;
    background-color: gainsboro;
}






