.Header {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
    "m";
    grid-area: H;
    background-color: lightskyblue;
    height: 100px;
    grid-column: 1/-1;
}

.menu {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "a a a a";
    grid-area: m;
    grid-column: 8/12;
    align-self: center;
    text-align: center;
    border: 2px solid black;
}
a {
    text-decoration: none;
    color: black;
}

a:hover {
    color: red;
}

