.Content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, auto);
    grid-template-areas:
    "resume"
    "skill"
    "jobs"
    "long"
    "contacts";
    grid-area: C;
    grid-column: 1/-1;
    background-color: blue;
    grid-row-gap: 2px;
}





