.contacts {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 500px;
    grid-template-areas:
            ". base base base base base base base base base base .";
    background-color: peachpuff;
    grid-column: 1/-1;
}

.base {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 20% 60% 20%;
    grid-template-areas:
            "sp6"
            "cont"
            "bt6";
    height: 400px;
    grid-column: -12/-2;
    align-self: center;
    border: 2px solid black;
}

.sp6 {
    background-color: gainsboro;
    grid-column: 6/8;
    align-self: start;
    height: 40px;
    text-align: center;
    color: black;
    font-weight:bold;
    font-size:20px;
}

.cont {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 20% 20% 60%;
    grid-template-areas:
        "in1"
        "in2"
        "t3";
    background-color: lightgray;
    grid-column: 4/10;
    grid-row-gap: 1px;

}

.bt6 {
    grid-column: 6/8;
    align-self: end;
    height: 50px;
    color: black;
    font-weight:bold;
    font-size:20px;
}

.in1 {
    grid-area: in1;
    grid-column: 1/7;
    grid-row: 1/2;
    background-color: darkgrey;
    font-size: 150%;
    color: red;
    font-weight: bold;
}

.in2 {
    grid-area: in2;
    grid-column: 1/7;
    grid-row: 2/3;
    background-color: darkgrey;
    color: red;
    font-size: 150%;
    font-weight: bold;
}

.t3 {
    grid-area: t3;
    grid-column: 1/7;
    grid-row: 3/4;
    background-color: darkgrey;
    color: red;
    font-size: 250%;
    font-weight: bold;
}

