.jobs {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 700px;
    grid-template-areas:
            ". base base base base base base base base base base .";
    background-color: lavender;
    grid-column: 1/-1;
}

.base {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 30% 70%;
    grid-template-areas:
            "sj1"
            "sj2";
    height: 600px;
    grid-column: -12/-2;
    align-self: center;
    border: 2px solid black;
}

.sj1 {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "sb11";
    grid-column: 1/-1;
}

.sj11 {
    grid-column: 4/6;
    align-self: start;
    font-weight:bold;
    font-size:20px;
    text-align: center;
    background-color: gainsboro;
    height: 30px;
}

.sj2 {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "sj21 sj21 . . sj22 sj22";
    grid-column: 1/-1;
    border-top: 2px solid black;
}

.sj21 {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 70% 30%;
    grid-template-areas:
            "sj21a"
           " sj21b";
    background-color: white;
    grid-column: 1/4;
    border: 2px solid red;
}

.sj21a {
    background-color: lightgray;
}

.sj21b {
    background-color: gainsboro;
}

.sj22 {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 70% 30%;
    grid-template-areas:
            "sj22a"
            "sj22b";
    background-color: white;
    grid-column: 6/9;
}

.sj22a {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 90% 10%;
    grid-template-areas:
            ". . . . . . . . . . . ."
            "bs";
    background: url("image3.jpg");
}
.bs {
    grid-column: 2/7;
    align-self: end;
    width: 150px;
    height: 40px;
    border: 2px solid blue;
    color: red;
    font-weight:bold;
    font-size:20px;
    background-color: lightskyblue;
    text-align: center;
}

.sj22b {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "jt";
    background-color: gainsboro;
}

.jt {
    grid-column: 2/12;
    font-weight:bold;
    font-size:20px;
}

.sp3 {
    background-color: darkgrey;
}

p {
    background-color: darkgrey;
    height: 70px;
}





