.long {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 200px;
    grid-template-areas:
            ". base base base base base base base base base base .";
    background-color: lightyellow;
    grid-column: 1/-1;
}

.base {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
            "sp4"
            "bt4";
    height: 100px;
    grid-column: -12/-2;
    align-self: center;
    border: 2px solid black;
}

.sp4 {
    background-color: gainsboro;
    grid-column: 4/10;
    color: black;
    font-weight:bold;
    font-size:25px;
    text-align: center;
    align-self: start;
}

.bt4 {
    background-color: darkgrey;
    grid-column: 6/8;
    color: black;
    font-weight:bold;
    font-size:20px;
}




