.resume {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 500px;
    grid-template-areas:
            ". base base base base base base base base base base .";
    background-color: peachpuff;
    grid-column: 1/-1;
}

.base {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 400px;
    grid-template-areas:
            "br1 br2";
    grid-column: -12/-2;
    align-self: center;
    border: 2px solid black;
}

.br1 {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "br11";
    grid-column: 1/7;
}

.br11 {
    grid-column: 2/6;
    background-color: gainsboro;
    align-self: center;
    font-weight:bold;
    font-size:18px;
    text-align: center;
    height: 30%;
}

.br2 {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            ". br21 br21 br21 br21 .";
    grid-column: 7/13;
    grid-row: 1/2;
}

.br21 {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas:
            "img img img img";
    grid-column: 2/6;
    grid-row: 1/2;
    grid-area: br21;
}

img {
    grid-area: img;
    grid-column: 1/5;
    grid-row: 1/2;
    height: 100%;
    width: 100%;
}






